import React, { Component } from 'react';
import style from "./style.scss";
import { Fa } from 'mdbreact';
import { distributionData } from '../data';
import classNames from 'classnames';

class ReviewStat extends Component {
  render() {
    return (
      <div className="container">
        <div className={classNames(style.reviewStat, 'row')}>
          <div className={classNames(style.averageBlock, 'col-md-3')}>
              <div>Average Rating</div>
              <div className={style.average}>5.0</div>
              <div>
                { [...Array(5)].map((item, index)=>
                  <Fa 
                    key={index}
                    icon='far fa-star'
                    className={style.icon}/>)}    
              </div>
              <div>(194 ratings)</div>
            </div>
          <div className='col-md-9 col-12' style={{flex: 1}}>
              { distributionData.map((item, index)=>
                  <div 
                    key={index} 
                    className={style.reviewRating}
                    style={{marginBottom: `${index===distributionData.length-1?0:'17px'}`}} >
                    <div>{item.tag}</div>
                    <div className={style.space}></div>
                    { [...Array(5)].map((item1, index)=>
                    <Fa 
                      key={index}
                      icon='far fa-star'
                      className={style.icon}
                      style={{color: `${index<=item.star?'#00b5e2':'#ccc'}`}}/>)} 
                    <div className={style.ratingCount}>{item.count}</div>
                  </div>
                )}
              
            </div>
        </div>
      </div>
    );
  }
}

export default ReviewStat;