import React, { Component } from 'react';
import style from "./style.scss";
import classNames from 'classnames';
import { connect } from 'react-redux';
import TabBar from './TabBar';
import TabBarResponsive from './TabBarResponsive';
import withSizes from "react-sizes";
import screenSize from "util/ScreenSize";


class Index extends Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const { width, fixed } = this.props;
    return (
      <div>
        <div className={fixed ? 'pb-5' : ''}></div>
        <div 
          className={classNames(style.root,
            fixed && style.fixed
          )}>
          { width > 1024 ? <TabBar/> : <TabBarResponsive /> }
        </div>
      </div>
    );
  }
}

const connectIndex = connect(
  (state)=>({
    fixed: state.tabBar.fixed
  })
)(Index);

const mapSizesToProps = ({ width }) => screenSize(width);

const withSizesIndex = withSizes(mapSizesToProps)(connectIndex);

export default withSizesIndex;