import React, { Component } from 'react';
import { Link, withRouter } from "react-router-dom";
import style from "./style.scss";
import classNames from 'classnames';
import { tabBarData }  from "../data";
import {checkCurrentUrl} from "util/Common";
import { Fa, Button } from 'mdbreact';

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    }
    this.toggleTabBar = this.toggleTabBar.bind(this);
  }

  toggleTabBar() {
    this.setState({ show: !this.state.show });
  }
  render() {
    const { match: { url } } = this.props;
    console.log(url);
    const { show } = this.state;
    const username = "thuc";

    return (
      <div className={classNames('d-flex', style.tabBarResponvive)}>
        {/* avatar */}
        <div className={classNames('d-flex', style.avatar)}>
          <img src="https://process.filestackapi.com/cache=expiry:max/resize=width:200/s1cURsYQRfuQi6a4pblb" alt=""/>
          <div className={style.online}>
            <Fa icon='fas fa-circle'style={{color: 'green', fontSize: 10}} />
          </div>
        </div>
        {/* avatar */}

        {/*toggle button*/}
        <div 
          className={classNames(
            'p-1 font-weight-bold',
            show ? style.toggleButtonActive : style.toggleButton
          )}
          onClick={this.toggleTabBar}>
          <Fa style={{marginRight: 10}} size="lg" icon="bars" />
          { tabBarData.map((item, index)=>
            <React.Fragment key={index}>
              {checkCurrentUrl(item.url, url, `/profile/${username}`) && item.title}
            </React.Fragment>)}
        </div>
        {/*toggle button*/}

        {/*tab bar list */}
        { show &&
          <div className={style.tabBarList}>
            { tabBarData.map((item, index)=>{
                let check = checkCurrentUrl(item.url, url, `/profile/${username}`);
                return (
                  <Link 
                    key={index}
                    className={classNames(
                      style.tabBarItemResponsive,
                      check &&  style.activeResponsive,
                    )}
                    to={`/profile/${username}${item.url}`}>
                    {item.name}
                    { item.count !== 0 && 
                      <div 
                        className={style.tabBarCount}
                        style={{backgroundColor: `${check?'rgba(255,255,255,0.2)':''}`}}>
                        {item.count}
                      </div>}
                  </Link>)
            })}
          </div>}
          {/*tab bar list */}

          <Button
            color="success"
            className="text-uppercase font-weight-bold "
            size="sm"
            style={{right: 10, position: 'absolute', margin: 0, padding: 10}}
          >
            <Fa icon="envelope" /> <span className="ml-1">message</span>
          </Button>
      </div>

    );
  }
}

export default withRouter(Index);
