import React, { Component } from 'react';
import style from "./style.scss";


class Comment extends Component {
  render() {
    const { comment } = this.props
    return (
      <div className="d-flex mb-3">
        <div className={style.commentAvatar}>
          <img src={comment.imgUrl} alt={comment.name}/>
        </div>
        <div className={style.commentDetail}>
          <div className={style.arrow}></div>
          <div className={style.content}>{comment.detail}</div>
          <div>
            <div className={style.name}>{comment.name}</div>
            <div className={style.date}>{comment.date}</div>
          </div>
        </div>
      </div>
    );
  }
}

export default Comment;