import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import style from "./style.scss";
import classNames from "classnames";
import { tabBarData } from "../data";
import {checkCurrentUrl} from "util/Common";

class TabBar extends Component {
  render() {
    const {
      match: { url }
    } = this.props;
    const username = "thuc";
    return (
      <div className={classNames("d-flex align-items-center", style.tabBar)}>
        {tabBarData.map((item, index) => (
          <Link
            onClick={this.focus}
            key={index}
            className={classNames(
              style.tabBarItem,
              checkCurrentUrl(item.url, url, `/profile/${username}`) &&
              style.active
            )}
            to={`/profile/${username}${item.url}`}
          >
            {item.name}
            {item.count !== 0 && (
              <div className={style.tabBarCount}>{item.count}</div>
            )}
          </Link>
        ))}
      </div>
    );
  }
}

export default withRouter(TabBar);
